import React from 'react';
import { Link } from 'react-router-dom';
import { Input, Button, Back } from '../../atoms';
import styles from '../../../styles/Join.module.scss';

interface JoinComponentProps {
    screenState: number;
    phone: string;
    certification: string;
    timer: string;
    errors: {
        phone: string;
        certification: string;
    };
    isActive: {
        getCode: boolean;
        start: boolean;
    };
    onChange: (e: React.ChangeEvent<HTMLInputElement>, type?: string) => void;
    onBlur: (e: React.FocusEvent<HTMLInputElement>) => void;
    onGetCodeBtnClick: () => void;
    onSubmitClick: () => void;
}

const JoinComponent = ({
    screenState,
    phone,
    certification,
    timer,
    errors,
    isActive,
    onChange,
    onBlur,
    onGetCodeBtnClick,
    onSubmitClick,
}: JoinComponentProps) => {
    return (
        <div id="component" className={styles.component}>
            <Back />
            <p className={styles.title}>  </p>
            <div className={styles.content}>
                <Input
                    props={{
                        type: 'text',
                        name: 'phone',
                        maxLength: 13,
                        placeholder: '  ',
                        value: phone,
                        onChange,
                        onBlur,
                    }}
                    error={screenState === 1 ? errors.phone : undefined}
                />
                <Button
                    props={{
                        text: screenState === 1 ? ' ' : `   ${timer !== '0:00' ? timer : ''}`,
                        isActive: isActive.getCode,
                        secondary: true,
                        onClick: onGetCodeBtnClick,
                    }}
                />
                {screenState === 1 && (
                    <div className={styles.help_area}>
                        <span>  ?</span>
                        <Link to={'/'}> </Link>
                    </div>
                )}
                {screenState === 2 && (
                    <>
                        <Input
                            props={{
                                type: 'number',
                                name: 'certification',
                                maxLength: 6,
                                placeholder: ' ',
                                value: certification,
                                onChange,
                                onBlur,
                            }}
                            error={errors.certification}
                            explanation={'    !'}
                        />
                        <div className={styles.submit}>
                            <Button props={{ text: '', isActive: isActive.start, onClick: onSubmitClick }} />
                        </div>
                    </>
                )}
            </div>
        </div>
    );
};

export default JoinComponent;
